'use client';

import { useState } from 'react';
import { useForm, type SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { identifyRelevantFutureSkills } from '@/app/actions';
import type { IdentifyFutureSkillsOutput } from '@/ai/flows/future-skills-identifier';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Badge } from '@/components/ui/badge';
import { Loader2, Lightbulb, AlertCircle } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

const formSchema = z.object({
  careerInterest: z.string().min(5, { message: 'Career interest must be at least 5 characters long.' }),
  yearsOfExperience: z.coerce.number().min(0, { message: 'Years of experience cannot be negative.' }).optional(),
});

type FormData = z.infer<typeof formSchema>;

export function FutureSkillsForm() {
  const [isLoading, setIsLoading] = useState(false);
  const [skillsResult, setSkillsResult] = useState<IdentifyFutureSkillsOutput | null>(null);
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      careerInterest: '',
    },
  });

  const onSubmit: SubmitHandler<FormData> = async (data) => {
    setIsLoading(true);
    setSkillsResult(null);
    setError(null);
    try {
      const inputData = {
        careerInterest: data.careerInterest,
        ...(data.yearsOfExperience !== undefined && { yearsOfExperience: data.yearsOfExperience }),
      };
      const result = await identifyRelevantFutureSkills(inputData);
      setSkillsResult(result);
      toast({
        title: "Skills Identified",
        description: "Future skills have been identified based on your input.",
      });
    } catch (e: any) {
      setError(e.message || 'An unexpected error occurred.');
      toast({
        title: "Error",
        description: e.message || "Failed to identify skills. Please try again.",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl text-primary flex items-center">
            <Lightbulb className="mr-2 h-6 w-6" />
            Identify Future Skills
          </CardTitle>
          <CardDescription>
            Enter your career interest and experience to discover skills crucial for your future success.
          </CardDescription>
        </CardHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)}>
            <CardContent className="space-y-4">
              <FormField
                control={form.control}
                name="careerInterest"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel htmlFor="careerInterest" className="text-lg">Career Interest / Current Job</FormLabel>
                    <FormControl>
                      <Input
                        id="careerInterest"
                        placeholder="e.g., Software Engineer, Data Analyst, Marketing Specialist"
                        {...field}
                        disabled={isLoading}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="yearsOfExperience"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel htmlFor="yearsOfExperience" className="text-lg">Years of Experience (Optional)</FormLabel>
                    <FormControl>
                      <Input
                        id="yearsOfExperience"
                        type="number"
                        placeholder="e.g., 3"
                        {...field}
                        value={field.value ?? ''} 
                        disabled={isLoading}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </CardContent>
            <CardFooter>
              <Button type="submit" disabled={isLoading} className="w-full sm:w-auto">
                {isLoading ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Identifying...
                  </>
                ) : (
                  'Identify Skills'
                )}
              </Button>
            </CardFooter>
          </form>
        </Form>
      </Card>

      {error && (
         <Alert variant="destructive">
           <AlertCircle className="h-4 w-4" />
           <AlertTitle>Error</AlertTitle>
           <AlertDescription>{error}</AlertDescription>
         </Alert>
      )}

      {skillsResult && (
        <Card className="shadow-lg animate-in fade-in duration-500">
          <CardHeader>
            <CardTitle className="text-xl text-primary">Identified Future Skills</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div>
              <h3 className="font-semibold text-md text-primary mb-2">Key Skills to Develop:</h3>
              <div className="flex flex-wrap gap-2">
                {skillsResult.futureSkills.map((skill, index) => (
                  <Badge key={index} variant="secondary" className="text-sm px-3 py-1 bg-accent text-accent-foreground">
                    {skill}
                  </Badge>
                ))}
              </div>
            </div>
            <div>
              <h3 className="font-semibold text-md text-primary mt-4">Explanation:</h3>
              <p className="text-muted-foreground whitespace-pre-wrap">{skillsResult.explanation}</p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
